* {
  padding: 0;
  margin: 0;
}

.TokenTable {
  display: -ms-flexbox;
  display: flex;
  width: inherit;
  border: 0px solid #cecece;
  border-radius: 5px;
  margin-bottom: 8px;
  height: 35px;
  -webkit-box-shadow: 1px 1px rgb(49, 57, 85);
          box-shadow: 1px 1px rgb(49, 57, 85);
  margin-right: 3px;
  background-color: #343c5a;
}
.icon {
  grid-area: icon;
  width: 23px;
  margin: auto;
  margin-left: 5px;
}
.coins {
  grid-area: coins;
  text-align: left;
  display: inline-block;
  width: 95px;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  margin: auto;
  font-size: 13px;
  font-weight: bold;
  color: #7a89b2;
}
.ticker {
  grid-area: ticker;
  width: 55px;
  text-align: left;
  display: inline-block;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  margin: auto;
  font-size: 13px;
  font-weight: bold;
  color: #7a89b2;
}
.balance {
  grid-area: balance;
  text-align: right;
  margin: auto;
  width: 80px;
  font-size: 13px;
  margin-right: 5px;
  font-weight: bold;
  color: #7a89b2;
}



