.inpt{
    display: -ms-flexbox;
    display: flex;
    height: 25px;
    margin-left: 30px;
    padding-bottom: 7px;
}
.text{
background-color: #343c5a;
border-radius: 5px;
border: 1px solid rgb(49, 57, 85);
width: 200px;
height: 20px;
color: #7A89B2;
font-weight: bold;
-o-text-overflow: ellipsis;
   text-overflow: ellipsis;
overflow: hidden;
outline:none;
padding-left: 3px;
padding-right: 3px;
}
.btn{
    width: 30px;
    height: 30px;
    /* padding-left: 5px;
    padding-top: 3px; */
    padding-bottom: 2px;
    color: #1EBBB5;
    background: none;
    border: none;
    font-size: medium;
    outline:none;
}
.solid{
    margin: 0;
    padding: 0;
    width:314px;
    position:absolute;
    left: 30px;
    border-top: 1px solid #1EBBB5;
    border-bottom: 1px solid #1EBBB5;
   
}


