.nav-menu {
  background-color: #161c2e;
  width: 30px;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  position: fixed;
  top: 0;
  left: 0;
  /* background-image: url("../images/5e141dda2c2877c896bc174b_dotbg2.png"); */
  /* background-size: 8px, 8px; */
}

.nav-text {
  display: -ms-flexbox;
  display: flex;
  padding: 8px 0px 8px 0px;
  height: 40px;
}

.nav-text a {
  text-decoration: none;
  color: #1EBBB5;
  font-size: 20px;
  width: 100vw;
  height: 70%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 3px 4px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #343c5a;
}
.nav-text a.clicked {
  background-color: #343c5a;
}

.nav-menu-items {
  width: 100%;
  padding-top: 50%;
}

span {
  margin-left: 0px;
}



