.table {
  width: calc(100% - 7px);
  padding-top: 2px;
  padding-bottom: 2px;
  margin-bottom: 10px;
  font-size: medium;
  border: 0px solid #cecece;
  border-radius: 5px;
  -webkit-box-shadow: 1px 1px rgb(49, 57, 85);
          box-shadow: 1px 1px rgb(49, 57, 85);
  background-color: #343c5a;
  margin-right: 3px;
}
.details {
  display: -ms-flexbox;
  display: flex;
  width: calc(100%);
  margin-bottom: 2px;
  margin-right: 3px;
}

.divide{
  border: none;
  height: 1px;
  /* border-top: 1px solid #8bb9b8;
    border-bottom: 1px solid #8bb9b8; */
    background-color: #222b47;
}

p {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  width: calc(100% - 20px);
  overflow-y: auto;
  white-space: pre-line;
}
.right {
  text-align: end;
  display: inline-block;
  overflow: hidden;
  margin-right: 10px;
  font-size: 13px;
  font-weight: bold;
  color: #7A89B2
}
.left {
  text-align: start;
  display: inline-block;
  overflow: hidden;
  margin-left: 10px;
  font-size: 13px;
  /* font-weight: bold; */
  color: #7A89B2
}



