.Main {
  margin-top: 10px;
}

.wallet {
  margin-top: 10px;
  max-height: 230px;
  overflow-y: auto;
  overflow-x: hidden;
  /* margin-bottom: 10px; */
}

::-webkit-scrollbar {
  width: 3px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #cecece;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #343c5a;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}



