.nftImage, 
.nftDesc, 
.nftTitle{
    width: 128px;
    margin-bottom: 5px;
}
.nftImage{
    height: 120px;
    -o-object-fit:contain;
       object-fit:contain;
    margin: auto;
    width: 100%;
}
.nftTitle{
    /* height: 35px; */
    max-height: 35px;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 5px;
    color: #7A89B2
}.nftDesc{
    height: 90px;
    margin-left: 5px;
    margin-right: 5px;
    color: #7A89B2;
    font-size: smaller;
    
}

.ntfTable{
    border-radius: 5px;
    border: 2px solid #1EBBB5 ;
    margin-top: 8px;
    width: 95%;
    /* width: 46%; */
    padding: 2px;
    display: -ms-flexbox;
    display: flex;
}


